.alltickets {
	position: absolute;
	background-color: white;
	width: 100%;
	height: 70vh;
	display: flex;
	justify-content: space-evenly;
	align-items: stretch;
	flex-wrap: wrap;
}

@media screen and (max-width: 768px) {
	.alltickets {
		width: 100%;
		top: 3rem;
	}
}


.product-details {
	background-color: white;
	padding-left: 5px;
	padding-right: 5px;
	height: 50px;
}

.product-brand {
	font-size: small;
	font-weight: bold;
	position: relative;
	top: -3rem;
}

.product-product {
	position: relative;
	font-size: small;
	top: -10px;
	position: relative;
	top: -4rem;
}

.product-discountedPrice {
	position: relative;
	font-size: small;
	top: -20px;
	left: -20px;
	position: relative;
	top: -4rem;
}

.product-discountPercentage {
	font-size: small;
	position: relative;
	top: -20px;
	left: -30px;
	color: orange;
	font-weight: 400;
	position: relative;
	top: -4rem;
}

.ticket {
	min-height: 400px;
	margin: 1rem;
	background-color: hsla(0, 0%, 100%, .8);
	height: 200px;
	width: 180px;
	min-width: 180px;
}

div .ticket:hover {
	box-shadow: 0 5px 12px 0 grey;
}

#idofwish:hover {
	border-color: deeppink;
}

#idofwish {
	background-color: white;
	position: relative;
	margin-left: 4px;
	font-weight: bold;
	height: 3rem;
	width: 10.7rem;
	border: lightgrey solid 1px;
	border-radius: 5px;
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	cursor: pointer;
	top: -3.5rem;
}

.no-items-found-image {
	height: 300px;
	width: 300px;
}

.image-container {
	height: 60%;
	width: 100%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.image-text-container {
	display: flex;
	flex-direction: column;
}

.text-image {
	margin: 0 auto;
}